














import Vue from 'vue';
import { mapState } from 'vuex';

export default Vue.extend({
	data() {
		return {
			todo_input: '',
		};
	},
	computed: {
		...mapState(['date']),
	},
	methods: {
		handleInput() {
			if (this.date === '') {
				return alert('Please select Date');
			} else {
				if (this.todo_input === '') {
					return alert('Input something');
				}
				this.$store.commit('addTodoItem', this.todo_input);
				this.todo_input = '';
			}
		},
	},
});
