















import Vue from 'vue';
export default Vue.extend({
	data() {
		return {
			date_tabs: ['Day', 'Week', 'Month', 'Year'],
			active_tab: 'Day',
		};
	},

	methods: {
		handleTab() {
			this.$store.commit('selectTab', this.active_tab);
		},
	},

	mounted() {
		this.handleTab();
	},
});
