

















import Vue from 'vue';
import { mapState } from 'vuex';

export default Vue.extend({
	data() {
		return {
			s_date: '',
		};
	},

	computed: {
		...mapState(['active_tab', 'date']),
	},

	methods: {
		handleDate() {
			this.$store.commit('selectDate', this.s_date);
		},
	},

	mounted() {
		this.s_date = this.date;
	},
});
